export const getDate = function (date: string) {
    let dateParts = date.split('.');
    let month = getMonth(dateParts[1]);
    return dateParts[2] + ' ' + month + ' ' + dateParts[0];
};

function getMonth(monthNumber: string): string {
    switch (monthNumber) {
        case '01':
          return 'January';
        case '02':
          return 'February';
        case '03':
          return 'March';
        case '04':
          return 'April';
        case '05':
          return 'May';
        case '06':
          return 'June';
        case '07':
          return 'July';
        case '08':
          return 'August';
        case '09':
          return 'September';
        case '10':
          return 'October';
        case '11':
          return 'November';
        case '12':
          return 'December';
        default: 
          return 'January';
    }
}